import { FacebookRounded, Google } from '@mui/icons-material';
import { Box, Button, Divider, Stack, Typography, useMediaQuery } from '@mui/material';
import Input from '~/components/Input';
import config from '~/config';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { EmailIcon, PasswordIcon, UserIcon } from '../Icon';
import userApi from '~/api/module/user.api';
function SingUp() {
    const pointDownSm = useMediaQuery((theme) => theme.breakpoints.down('sm'));
    const formik = useFormik({
        initialValues: {
            name: '',
            email: '',
            password: '',
            confirmPassword: '',
        },
        validationSchema: Yup.object({
            name: Yup.string().min(8, 'Tn ng nhp phi ti thiu 8 k t').required('Vui lng nhp tn ng nhp'),
            email: Yup.string().email('nh dng email khng ng').required('Vui lng nhp email'),
            password: Yup.string()
                .min(8, 'Mt khu phi cha t nht 8 k t')
                .matches(/[0-9]/, 'Mt khu phi cha ch s')
                .matches(/[a-z]/, 'Mt khu phi cha ch ci thng')
                .matches(/[A-Z]/, 'Mt khu phi cha ch ci hoa')
                .matches(/[^\w]/, 'Mt khu phi cha k t c bit')
                .matches(/^(\S+$)/, 'Mt khu khng khong trng')
                .required('Vui lng nhp mt khu'),
            confirmPassword: Yup.string()
                .oneOf([Yup.ref('password')], 'Nhp li mt khu cha chnh xc')
                .required('Vui lng nhp li mt khu'),
        }),
        onSubmit: async (values) => {
            const { response, err } = await userApi.signup(values);
        },
    });
    return (
        <Box component={'form'} onSubmit={formik.handleSubmit}>
            <Stack spacing={2} mt={3}>
                <Input
                    type="text"
                    name="name"
                    placeholder={'Tn ng nhp'}
                    value={formik.values.name}
                    onChange={formik.handleChange}
                    error={formik.errors.name !== undefined && formik.touched.name}
                    helperText={formik.touched.name && formik.errors.name}
                    leftIcon={<UserIcon/>}
                ></Input>
                <Input
                    type="text"
                    name="email"
                    placeholder={'Email'}
                    value={formik.values.email}
                    onChange={formik.handleChange}
                    error={formik.errors.email !== undefined && formik.touched.email}
                    helperText={formik.touched.email && formik.errors.email}
                    leftIcon={<EmailIcon/>}
                ></Input>
                <Input
                    type="password"
                    name="password"
                    placeholder={'Mt khu'}
                    value={formik.values.password}
                    onChange={formik.handleChange}
                    error={formik.errors.password !== undefined && formik.touched.password}
                    helperText={formik.touched.password && formik.errors.password}
                    leftIcon={<PasswordIcon/>}
                ></Input>
                <Input
                    type="password"
                    name="confirmPassword"
                    placeholder={'Nhp li mt khu'}
                    value={formik.values.confirmPassword}
                    onChange={formik.handleChange}
                    error={formik.errors.confirmPassword !== undefined && formik.touched.confirmPassword}
                    helperText={formik.touched.confirmPassword && formik.errors.confirmPassword}
                    leftIcon={<PasswordIcon/>}
                ></Input>

                {/* button */}
                <Button type={'submit'} variant="contained" sx={{ borderRadius: '100px' }} size={pointDownSm ? 'small' : 'medium'}>
                    ng K Ngay
                </Button>
                <Divider
                    sx={{ '::before, :: after': { borderTop: '1px solid rgba(255, 255, 255, 0.2)' }, fontSize: '16px' }}
                >
                    Hoc
                </Divider>
                <Button variant="pill-outline" color="white-outline" startIcon={<Google />} size={pointDownSm ? 'small' : 'medium'}>
                    <div>ng nhp bng Google</div>
                </Button>
                <Button variant="pill-outline" color="white-outline" startIcon={<FacebookRounded />} size={pointDownSm ? 'small' : 'medium'}>
                    <span>ng nhp bng Facebook</span>
                </Button>
            </Stack>
            {/* button */}
            <Stack direction={'row'} justifyContent={'space-between'} mt={2}>
                <Typography variant="subtitle2" component={'a'} href={config.routes.login}>
                    Bn  c ti khon? ng Nhp
                </Typography>
            </Stack>
        </Box>
    );
}

export default SingUp;
